import merchantItem from '@/pages/home/index/children/popular-item'
import empty from '@/components/empty'
import { mapState, mapActions } from 'vuex'
export default {
	name: '',
	data () {
		return {
			type: ['综合排序', '距离最近', '信誉最高'],
			current: 0
		}
	},
	onLoad () {
		this._reqGetMerchantList()
	},
	onShow () {
		this.$hideTabbarButton()
	},
	computed: {
		...mapState(['merchant_list']),
	},
	methods: {
		...mapActions(['reqGetMerchantList']),
		_reqGetMerchantList () {
			let order;
			switch (this.current) {
				case 0: order = 1;
				break;
				case 1: order = 3;
				break;
				case 2: order = 2;
				break;
			}
			this.reqGetMerchantList({order: order})
		},
		onToGoodsDetail (id) {
			uni.navigateTo({
				url: '/pages/home/subPages/merchant-detail/merchant-detail?id=' + id
			})
		},
		onTypeClick (index) {
			if(this.current !== index) {
				this.current = index;
				this._reqGetMerchantList()
			}
		}
	},
	components: {
		merchantItem,
		empty
	}
}
